@import url(https://fonts.googleapis.com/css?family=Josefin+Slab:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap);
@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Josefin+Slab:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap);
.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.task-input {
  width: 350px;
  min-width: 250px;
  padding: 15px 10px 10px 20px;
  margin-right: 10px;
  font-size: 15px;
  color: #ccc;
  background-color: #1d222b;
  border: 1px solid #444;
  outline: none;
  border-radius: 20px;
  box-shadow: 0 1px 2px rgba(0,0,0,0.3);
}

.date-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.date-wrapper label, .buttons-checks label  {
  font-size: 16px;
  padding-right: 10px;
  color: #ccc;
}

.date-wrapper input {
  background-color: silver;
  padding: 0 20px;
  box-shadow: 0 1px 2px rgba(0,0,0,0.3);
  border: 1px solid #444;
  outline: none;
  border-radius: 20px;
}

::-webkit-datetime-edit-month-field { color: #790e43; }
::-webkit-datetime-edit-day-field { color: #790e43; }
::-webkit-datetime-edit-year-field { color: #790e43; }
::-webkit-inner-spin-button { display: none; }


.buttons-checks {
  display: flex;
}

.btn {
  margin: 10px;
}

.add-task-btn {
  min-width: 100px;
  background-color: #790e43;
  color: #ccc;
  border: none;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.3);
  outline: none;
  cursor: pointer;
}

@media(max-width: 499px) {
  .task-input  {
    width: 280px;
  }
}
.list-item, .list-item-done {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  padding-bottom: 5px;
}

.task-name, .task-name-done {
  color: #999;
  min-width: 60%;
  text-align: center;
}

.list-item-done em, .list-item-done span, .list-item-done p {
  font-size: 12px;
}

.task-btn {
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
}

.btn-done i, .btn-delete i {
  font-size: 14px;
  color: #bbb;
  margin-right: 20px;
}

@media(max-width: 899px) {
  .task-name {
    font-size: 14px;
  }
}

@media(max-width: 599px) {
  .task-name {
    font-size: 10px;
  }
  .btn-delete i, .btn-done i {
    font-size: 10px;
  }
  .list-item span {
    font-size: 12px;
  }
  .list-item-done em, .list-item-done span, .list-item-done p {
    font-size: 10px;
  }
}

@media(max-width: 499px) {

  .date-tasks {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
    padding-bottom: 5px;
  }

  .task-name {
    font-size: 12px;
    padding: 10px
  }

  .list-item-done {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 10px;
  }

  .list-item-done p, .list-item-done span, .list-item-done em {
    padding: 5px;
    font-size: 14px;
  }

  .list-item-done .btn-delete i {
    font-size: 14px;
  }

  /* .date-tasks span {
    text-align: center;
  } */
}
.list {
  width: 90%;
  margin: auto;
  max-height: 400px;
  overflow: hidden;
  overflow-y: auto;
}

.active  p {
  color: #777;
  text-align: center;
  font-size: 18px;
  margin-top: 20px;
}
* {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', serif;
  box-sizing: border-box;
}

.container {
  background: linear-gradient(100deg, rgb(182, 40, 111) 50%, #ac2066 0);
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper {
  position: relative;
  background-color: #292f38;
  width: 55%;
  min-width: 750px;
  height: 800px;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 15px 30px rgba(0,0,0,0.4);
}

.app-wrapper footer {

  position: absolute;
  width: 100%;
  bottom: 0;
  text-align: center;
  color: #ccc;
  padding: 10px;
}

.app-wrapper h1,h3 {
  color: #ccc;
  font-weight: 300;
  text-align: center;
  margin: 50px 20px 60px 20px;
  font-family: 'Josefin Slab', serif;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: space-between;
  margin-bottom: 50px; 
  width: 100%;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #aaa;
}

::-webkit-scrollbar-thumb {
  background: #444;
  border-radius: 10px;
}

@media(max-width: 899px) {
  .app-wrapper {
    min-width: 560px;
    padding: 15px;
  }
}

@media(max-width: 599px) {
  .app-wrapper {
    min-width: 400px;
    padding: 0;
  }
}

@media(max-width: 499px) {
  .app-wrapper {
    min-width: 350px;
    padding: 0;
    height: 99vh;
  }
}
